* {
    box-sizing: border-box;
  }
  
  body {
    margin: 0;
    background-color: #F5F7FB;
    /* background-image: url(logo512.png); */
  }
  
  .questions {
    text-align: center;
    padding: 50px;
    padding-top: 20px; 
  
  }
  
  .main {
    padding: 1px;
    border-radius: 20px;
    background-color: rgb(255, 255, 248);
    margin-left: 80px;
    margin-right: 80px;
    /* text-align: center; */
  }    
  
  h2 {
    /* font-size: 20px; */
    color: #1a4fd4;
    /* font-family: "Karla", sans-serif; */
    font-weight: 700; 
  }
  
  
  .button {
    margin: 1.5%;
    height: 30px;
    border-radius: 10px;
    border: solid 1.3px #4D5B9E;
    background-color: #F5F7FB;
    cursor: pointer;
    color: #293264;
    min-width: 90px;
    font-size: 19px;
    font-weight: 500;
    
  }
  
  
  
  
  hr {
    /* margin-top: 20px; */
    background-color: #DBDEF0;
    max-width: 700px;
  }
  
  .check-button {
    width: 120px;
    height: 35px;
    background: #4D5B9E;
    border-radius: 10px;
    border: solid 0px #4D5B9E;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: px;
    line-height: 12px;
    text-align: center;
    cursor: pointer;
    color: #F5F7FB;
    margin: 20px;
  }
  
  span {
    font-size: 22px;
    font-weight: bold;
    vertical-align: text-top;
  }
  
  .score {
    background-color: lightgreen;
    border-radius: 50px;
    /* max-width: 500px; */
    
  }
  
  h1 {
    color: rebeccapurple;
    
  }